"use client";
import Checkbox from "@/components/form/input/Checkbox";
import Input from "@/components/form/input/InputField";
import Label from "@/components/form/Label";
import Button from "@/components/ui/button/Button";
import { ChevronLeftIcon, EyeCloseIcon, EyeIcon } from "@/icons";
import Link from "next/link";
import { useRouter, useSearchParams } from 'next/navigation';
import React, { useState } from "react";
import { services } from '@/services/api';
import { toast } from 'react-hot-toast';
import { cookies } from '@/utils/cookies';

export default function SignInForm() {
  const router = useRouter();
  const searchParams = useSearchParams();
  const [showPassword, setShowPassword] = useState(false);
  const [isChecked, setIsChecked] = useState(false);
  const [loading, setLoading] = useState(false);
  const [resetModalOpen, setResetModalOpen] = useState(false);
  const [resetEmail, setResetEmail] = useState('');
  const [resetLoading, setResetLoading] = useState(false);
  const [formData, setFormData] = useState({
    email: '',
    password: '',
  });
  const [fieldErrors, setFieldErrors] = useState<Record<string, string>>({});

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!formData.email || !formData.password) {
      toast.error('Por favor, preencha todos os campos');
      return;
    }

    // Limpar erros anteriores
    setFieldErrors({});
    setLoading(true);

    try {
      const response = await services.auth.login({
        email: formData.email,
        senha: formData.password,
      });

      toast.success('Login realizado com sucesso!');
      
      // Pequeno delay para garantir que o usuário veja a mensagem de sucesso
      await new Promise(resolve => setTimeout(resolve, 1000));

      // Salvar token e dados do usuário em cookies
      cookies.token.set(response.data.token);
      cookies.user.set(JSON.stringify(response.data.user));

      // Obter URL de redirecionamento
      const redirect = searchParams.get('redirect');
      const defaultPath = {
      SUPERADMIN: "/dashboard/admin",
      ADMIN: "/dashboard/empresa",
      GESTOR: "/dashboard/empresa",
      VENDEDOR: "/dashboard/empresa",
      CLIENTE: "/dashboard/cliente"
      }[response.data.user.role] || "/";

      // Usar o caminho do redirect se existir, caso contrário usar o caminho padrão
      const targetPath = redirect ? decodeURIComponent(redirect) : defaultPath;

      // Aguardar o toast e depois redirecionar
      setTimeout(() => {
        if (typeof window !== 'undefined') {
          // window.location.href = targetPath;
          router.push(targetPath);
          setTimeout(() => {
            window.location.reload();
          }, 1500);
        }
      }, 1500);
    } catch (error: any) {
      console.log('error:', error);
      
      // Extrair erros de validação dos detalhes
      if (error.response?.data?.details && Array.isArray(error.response.data.details)) {
        const errors: Record<string, string> = {};
        error.response.data.details.forEach((detail: { field: string; message: string }) => {
          // Remover o prefixo "body." do campo e mapear "senha" para "password"
          let fieldName = detail.field.replace('body.', '');
          // Mapear "senha" para "password" para corresponder ao estado do formulário
          if (fieldName === 'senha') {
            fieldName = 'password';
          }
          errors[fieldName] = detail.message;
        });
        setFieldErrors(errors);
      }
      
      const errorMessage = error.response?.data?.message || error.response?.data?.error || 'Erro ao fazer login';
      toast.error(errorMessage, {
        duration: 3000,
      });
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (field: string, value: string) => {
    setFormData({ ...formData, [field]: value });
    // Limpar erro do campo quando o usuário começar a digitar
    if (fieldErrors[field]) {
      setFieldErrors({ ...fieldErrors, [field]: '' });
    }
  };
  return (
    <>
    <div className="flex flex-col flex-1 lg:w-1/2 w-full">
      <div className="w-full max-w-md sm:pt-10 mx-auto mb-5">
        <Link
          href="/"
          className="inline-flex items-center text-sm text-gray-500 transition-colors hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-300"
        >
          <ChevronLeftIcon />
          Voltar
        </Link>
      </div>
      <div className="flex flex-col justify-center flex-1 w-full max-w-md mx-auto">
        <div>
          <div className="mb-5 sm:mb-8">
            <h1 className="mb-2 font-semibold text-gray-800 text-title-sm dark:text-white/90 sm:text-title-md">
              Entrar no Sistema
            </h1>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Digite seu email e senha para acessar!
            </p>
          </div>
          <div>
            <form onSubmit={handleSubmit}>
              <div className="space-y-6">
                <div>
                  <Label>
                    Email <span className="text-error-500">*</span>{" "}
                  </Label>
                  <Input 
                    placeholder="Digite seu email" 
                    type="email" 
                    value={formData.email}
                    onChange={(e) => handleInputChange('email', e.target.value)}
                    error={fieldErrors.email || false}
                    required
                  />
                </div>
                <div>
                  <Label>
                    Senha <span className="text-error-500">*</span>{" "}
                  </Label>
                  <div className="relative">
                    <Input
                      type={showPassword ? "text" : "password"}
                      placeholder="Digite sua senha"
                      value={formData.password}
                      onChange={(e) => handleInputChange('password', e.target.value)}
                      error={!!fieldErrors.password}
                      required
                    />
                    <span
                      onClick={() => setShowPassword(!showPassword)}
                      className="absolute z-30 -translate-y-1/2 cursor-pointer right-4 top-1/2"
                    >
                      {showPassword ? (
                        <EyeIcon className="fill-gray-500 dark:fill-gray-400" />
                      ) : (
                        <EyeCloseIcon className="fill-gray-500 dark:fill-gray-400" />
                      )}
                    </span>
                  </div>
                  {fieldErrors.password && (
                    <p className="mt-1 text-xs text-error-500">{fieldErrors.password}</p>
                  )}
                </div>
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-3">
                    <Checkbox checked={isChecked} onChange={setIsChecked} />
                    <span className="block font-normal text-gray-700 text-theme-sm dark:text-gray-400">
                      Manter conectado
                    </span>
                  </div>
                  <button
                    type="button"
                    onClick={() => {
                      setResetEmail(formData.email);
                      setResetModalOpen(true);
                    }}
                    className="text-sm text-brand-500 hover:text-brand-600 dark:text-brand-400"
                  >
                    Esqueceu sua senha?
                  </button>
                </div>
                <div>
                  <Button 
                    className="w-full" 
                    size="sm"
                    type="submit"
                    disabled={loading}
                  >
                    {loading ? 'Entrando...' : 'Entrar'}
                  </Button>
                </div>
              </div>
            </form>

            <div className="mt-5">
              <p className="text-sm font-normal text-center text-gray-700 dark:text-gray-400 sm:text-start">
                Não tem uma conta? {""}
                <Link
                  href="/auth/register"
                  className="text-brand-500 hover:text-brand-600 dark:text-brand-400"
                >
                  Cadastre-se
                </Link>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
      {resetModalOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-gray-900/50 px-4">
          <div className="w-full max-w-md rounded-2xl bg-white p-6 shadow-xl dark:bg-gray-900">
            <h2 className="text-lg font-semibold text-gray-900 dark:text-white">Recuperar acesso</h2>
            <p className="mt-2 text-sm text-gray-600 dark:text-gray-400">
              Informe o email cadastrado. Enviaremos um link seguro para o seu WhatsApp.
            </p>
            <form
              className="mt-6 space-y-4"
              onSubmit={async (e) => {
                e.preventDefault();
                if (!resetEmail) {
                  toast.error('Informe um email válido.');
                  return;
                }
                setResetLoading(true);
                try {
                  const response = await services.auth.resetPassword({ email: resetEmail });
                  toast.success(response.message || 'Link enviado com sucesso!');
                  setResetModalOpen(false);
                } catch (error: any) {
                  const message =
                    error.response?.data?.error ||
                    error.response?.data?.message ||
                    'Não foi possível enviar o link. Tente novamente mais tarde.';
                  toast.error(message);
                } finally {
                  setResetLoading(false);
                }
              }}
            >
              <div>
                <Label>Email</Label>
                <Input
                  type="email"
                  value={resetEmail}
                  onChange={(e) => setResetEmail(e.target.value)}
                  placeholder="email@empresa.com"
                  required
                />
              </div>
              <div className="flex justify-end gap-3">
                <Button
                  variant="outline"
                  size="sm"
                  type="button"
                  onClick={() => {
                    if (!resetLoading) {
                      setResetModalOpen(false);
                    }
                  }}
                >
                  Cancelar
                </Button>
                <Button
                  size="sm"
                  type="submit"
                  disabled={resetLoading}
                  className="min-w-[120px]"
                >
                  {resetLoading ? 'Enviando...' : 'Enviar link'}
                </Button>
              </div>
            </form>
          </div>
        </div>
      )}
    </>
  );
}
